import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { logout } from "../../actions/securityActions";
import Logo from "../../UI/Logo";
import alwazLogo from "../../UI/alwazLogo1.png";
import amdocsLogo from "../../UI/amdocs.jpg"
class Header extends Component {
  logout() {
    this.props.logout();
    window.location.href = "/";
  }
  render() {
    const { validToken, user } = this.props.security;

    const userIsAuthenticated = (
      <div className="collapse navbar-collapse" id="mobile-nav">
        <ul className="navbar-nav mr-auto">
          <li className="nav-item">
            <Link className="nav-link" to="/dashboard">
              תיקים
            </Link>
          </li>
        </ul>

        <ul className="navbar-nav ml-auto">
          <li className="nav-item">
            <Link className="nav-link " to="/dashboard">
              <i className="fas fa-user-circle mr-1" />
              {user.fullName}
            </Link>
          </li>
          <li className="nav-item">
            <Link
              className="nav-link"
              to="/logout"
              onClick={this.logout.bind(this)}
            >
              יציאה
            </Link>
          </li>
        </ul>
      </div>
    );

    const userIsNotAuthenticated = (
      <div className="collapse navbar-collapse" id="mobile-nav">
        <ul className="navbar-nav ml-auto">
          <li className="nav-item">
            <Link className="nav-link " to="/register">
              הירשם
            </Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/login">
              כניסה
            </Link>
          </li>
        </ul>
      </div>
    );

    const setGeneralLogo = "";

    const setAlwazLogo = <Logo logoImage={alwazLogo} />;
    const setBillalLogo = <Logo logoImage={amdocsLogo} />;

    const billal = "Billal Taha";
    const alwaz = "Alwaz Dahli Taha"
    let headerLinks;

    let dynamicLogo;

    if(user.fullName === alwaz){
      dynamicLogo = setAlwazLogo;
    }else if(user.fullName === billal){
      dynamicLogo = setBillalLogo;
    }
    else{
      dynamicLogo = setGeneralLogo;
    }

    if (validToken && user ) {
      headerLinks = userIsAuthenticated;
      
    }
     else {
      headerLinks = userIsNotAuthenticated;
      
    }
    return (
      <nav className="navbar navbar-expand-sm navbar-dark bg-primary mb-4">
        <div> {dynamicLogo} </div>

        <div className="container">
          <Link className="navbar-brand" to="/">
            ניהול תיקי לוקוחות למשרד עורכי דין
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#mobile-nav"
          >
            <span className="navbar-toggler-icon" />
          </button>
          {headerLinks}
        </div>
      </nav>
    );
  }
}

Header.propTypes = {
  logout: PropTypes.func.isRequired,
  security: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  security: state.security
});
export default connect(mapStateToProps, { logout })(Header);
